    .tour-countries {
      margin-bottom: 100px; } }

.tour-countries__item {
  display: flex; }
  @media (max-width: 1023.8px) {
    .tour-countries__item {
      flex-direction: column; } }

.tour-countries__content {
  max-width: 370px;
  margin-right: 100px;
  font-size: 16px;
  line-height: 28px;
  color: var(--color-tabs-a70); }
  @media (max-width: 1023.8px) {
    .tour-countries__content {
      max-width: none;
      margin: 0 auto;
      margin-bottom: 20px; } }
  @media (max-width: 767.8px) {
    .tour-countries__content {
      margin-bottom: 40px; } }

.tour-countries__title {
  margin-bottom: 30px;
  padding-top: 15px; }
  .tour-countries__title::after {
    width: 175px;
    height: 3px;
    margin-top: 19px; }
  @media (max-width: 1023.8px) {
    .tour-countries__title {
      margin-bottom: 20px; } }

.tour-countries__text {
  margin: 0;
  margin-bottom: 50px; }
  @media (max-width: 1023.8px) {
    .tour-countries__text {
      margin-bottom: 20px; } }

.tour-countries__button {
  min-width: 250px; }
  @media (max-width: 1023.8px) {
