.tour-countries__img-wrapper {
  width: 430px; }
  @media (max-width: 1023.8px) {
    .tour-countries__img-wrapper {
      width: calc(100% + 40px);
      margin: 0 auto;
      margin-left: -20px; } }

.tour-countries__img {
  width: 100%;
  height: 415px;
  object-fit: cover; }

.tour-testimonial {
