      left: calc(50% - 134px);
      text-align: center;
      padding-right: 20px;
      padding-left: 20px; } }

.tour-testimonial__title {
  margin: 0;
  margin-bottom: 20px;
  font-weight: 500;
  font-size: 30px;
  line-height: 37px;
  letter-spacing: -0.7px;
  color: var(--color-darkness); }
  @media (max-width: 767.8px) {
    .tour-testimonial__title {
      font-size: 20px;
      line-height: 24px; } }
  .tour-testimonial__title::after {
    content: "";
    display: block;
    width: 68px;
    height: 2px;
    margin-top: 11px;
    background-color: var(--color-accent); }
    @media (max-width: 767.8px) {
      .tour-testimonial__title::after {
        height: 1px;
        margin-left: 80px; } }

.tour-testimonial__item {
  margin: 0;
  font-style: normal; }

.tour-testimonial__text {
  margin-bottom: 31px;
  font-weight: normal;
  font-size: 14px;
  line-height: 28px;
  letter-spacing: -0.4px; }
  @media (max-width: 767.8px) {
    .tour-testimonial__text {
      margin-bottom: 18px; } }

.tour-testimonial__author {
  display: flex;
  align-items: center; }
  @media (max-width: 767.8px) {
    .tour-testimonial__author {
      flex-direction: column; } }

.tour-testimonial__avatar {
  width: 38px;
  height: 38px;
  left: 0;
  border-radius: 50%;
  overflow: hidden; }
  @media (max-width: 767.8px) {
    .tour-testimonial__avatar {
      margin-bottom: 12px; } }

.tour-testimonial__name {
  padding-left: 16px;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px; }
  @media (max-width: 767.8px) {
    .tour-testimonial__name {
      padding-left: 0; } }

.footer {
  font-size: 14px;
  line-height: 17px;
  color: var(--color-black-a50);
  letter-spacing: normal; }

.footer__container {
  display: grid;
  margin-top: 24px;
  padding: 0;
  grid-template-columns: 408px 390px 1fr; }
  @media (max-width: 1023.8px) {
    .footer__container {
      grid-template-columns: 54.5% 45.5%;
      padding: 0 20px; } }
  @media (max-width: 767.8px) {
    .footer__container {
      display: flex;
      padding: 0 21px 0 11px;
      flex-direction: column; } }

.footer__col {
  position: relative;
  margin-left: 20px;
  margin-bottom: 50px; }
  @media (max-width: 1023.8px) {
    .footer__col {
      margin-bottom: 20px;
      margin-left: 0; } }
  @media (max-width: 767.8px) {
    .footer__col {
      margin-bottom: 30px;
      text-align: center; } }
  .footer__col::after {
    content: "";
    display: block;
    position: absolute;
    width: 1px;
    height: 100%;
    top: 0;
    right: 0;
    background-color: var(--color-divider); }
    @media (max-width: 767.8px) {
      .footer__col::after {
        display: block;
        top: auto;
        right: 0;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 1px;
        background-color: var(--color-divider); } }
  .footer__col:nth-child(2) {
    margin-left: 90px; }
    @media (max-width: 1023.8px) {
      .footer__col:nth-child(2) {
        margin-left: 50px; }
        .footer__col:nth-child(2)::after {
          display: none; } }
    @media (max-width: 767.8px) {
      .footer__col:nth-child(2) {
        margin-left: 0; } }
  .footer__col:nth-child(3), .footer__col:last-child {
    margin-left: 49px; }
    @media (max-width: 1023.8px) {
      .footer__col:nth-child(3), .footer__col:last-child {
        margin-left: 0;
        padding-top: 30px;
        grid-column: 1/-1;
        text-align: center; } }
    @media (max-width: 767.8px) {
      .footer__col:nth-child(3), .footer__col:last-child {
        padding-top: 0; } }
    .footer__col:nth-child(3)::after, .footer__col:last-child::after {
      display: none; }
      @media (max-width: 1023.8px) {
        .footer__col:nth-child(3)::after, .footer__col:last-child::after {
          display: block;
          top: 0;
          right: 0;
          left: 0;
          width: 100%;
          height: 1px;
          background-color: var(--color-divider); } }
      @media (max-width: 767.8px) {
        .footer__col:nth-child(3)::after, .footer__col:last-child::after {
          top: -30px; } }

.footer__trips {
  display: flex; }
  @media (max-width: 767.8px) {
    .footer__trips {
      flex-wrap: wrap;
      justify-content: space-around; } }

.footer__contacts {
  display: flex;
  flex-direction: column; }
  @media (max-width: 767.8px) {
    .footer__contacts {
      align-items: center; } }

.footer__title {
  margin: 0;
  margin-bottom: 20px;
  font-weight: 600;
  font-size: 18px;
  line-height: 32px;
  color: var(--color-black); }

.footer__list {
  min-width: 80px;
  margin: 0;
  padding: 0;
  list-style: none;
  margin-right: 50px; }
  .footer__list:last-child {
    margin-right: 0;
    margin-left: -6px; }
  @media (max-width: 1023.8px) {
    .footer__list {
      margin-right: 46px; }
      .footer__list:last-child {
        margin-left: 0; } }
  @media (max-width: 767.8px) {
    .footer__list {
      min-width: 134px;
      margin-right: 20px;
      margin-bottom: 10px; }
      .footer__list:nth-child(2n) {
        margin-right: 0; } }

.footer__item {
  margin-bottom: 10px; }

.footer__link {
  color: inherit; }
  .footer__link:hover, .footer__link:active {
    transition: color 0.2s ease-out;
    color: var(--color-accent); }

.footer__contact-link {
  display: flex;
  position: relative;
  margin-bottom: 16px;
  align-items: center;
  color: var(--color-accent);
  text-decoration: underline; }
  .footer__contact-link--phone {
    margin-left: -3px;
    font-size: 16px;
    line-height: 32px; }

.footer__contact-img {
  display: block;
  margin-right: 15px; }

.footer__mail-text-mobile {
  display: none; }
  @media (max-width: 1023.8px) {
    .footer__mail-text-mobile {
      display: block; } }

@media (max-width: 1023.8px) {
  .footer__mail-text-desktop {
    display: none; } }

.footer__text {
  margin-bottom: 0;
  line-height: 24px; }

.footer__contact-link--mail .footer__contact-img {
  width: 26px;
  height: 20px; }

.footer__contact-link--phone .footer__contact-img {
  width: 30px;
  height: 30px; }

.footer__contact-link:hover,
.footer__contact-link:active {
  transition: color 0.2s ease-out;
  color: var(--color-darkness); }

.footer__contact-link--mail:hover path,
.footer__contact-link--mail:active path {
  transition: fill 0.2s ease-out;
  fill: var(--color-darkness); }

.footer__contact-link--phone:hover path,
.footer__contact-link--phone:active path {
  transition: stroke 0.2s ease-out;
  stroke: var(--color-darkness); }
